import React from "react"
import {graphql} from "gatsby"
import Layout from '../components/Layout'
import Jumbotron from "../components/Jumbotron"
import Rows from "../components/Rows"
import {fetchHomePage} from '../api'

// const sortByDate = movie => movie.sort((a, b) => new Date(a.movie.release) - new Date(b.movie.release))

const getLastReleased = movies => movies.filter(i => {
  let date = new Date(i.movie.release)
  let now = Date.now()
  return date <= now
})

const getComingSoon = movies => movies.filter(i => {
  let date = new Date(i.movie.release)
  let now = Date.now()
  return date >= now
})

const getPopular = movies => movies.sort((a,b) => b.analytics.views_last_month - a.analytics.views_last_month )

const getStreaming = movies => movies.filter(i => {
  let hasMovie = false
  i.videos.forEach(video => {
    if (video.type === "movie") hasMovie = true
  })
  return hasMovie
})

class HomePage extends React.Component {
  state = {
    movies: [],
  }

  // async componentDidMount(){
  //   const result = await fetchHomePage()
  //   console.log(result)
  //   this.setState({
  //     movies: result,
  //   })
  // }

  render() {
    let allMovies = this.props.data.allInternalMovies.edges.map(i =>  i.node).filter(i => i.title !== null)
    
    // Temporarily remove non streamable movies
    allMovies = getStreaming(allMovies)

    const lastReleased = getLastReleased(allMovies)
    // const comingSoon = getComingSoon(allMovies)
    const populaire = getPopular(allMovies)
    // const streaming = getStreaming(allMovies)
    return(
      <Layout>
        <Jumbotron/>
        <Rows movies={populaire} title={'Populaire'}/>
        <Rows movies={lastReleased} title={'Dernières sorties'}/>
        {/* <Rows movies={comingSoon} title={'Prochaines sorties'}/> */}
        {/* <Rows movies={streaming} title={'Streaming'}/> */}
      </Layout>
    )
  }
}

export default HomePage

export const query = graphql`
{
	allInternalMovies{
    edges{
      node{
        id
        title
        slug
        language
        synopsis
        poster
        videos {
          type
        }
        analytics {
          views_last_month
          views_last_three_days
          total_views
        }
        movie {
          alternative_id
          original_title
          original_language
          original_country
          budget
          box_office
          runtime
          release
          genres
        }
      }
    }
  }
}
`

