import React from 'react'
import { Link } from "gatsby"
import Slider from 'react-slick'
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"

import stallineJPG from '../images/mort.de.stalline.jpg'
import morganeJPG from '../images/poster_morganEtSesNymphes.jpg'


const NextArrow = ({ onClick }) => <button type="button" className="movie-carousel-next" onClick={onClick} ></button>
const PrevArrow = ({ onClick }) => <button type="button" className="movie-carousel-prev" onClick={onClick} ></button>

export default class Row extends React.Component {
  render() {
    const settings = {
      className:"col-12",
      slidesToShow: 7,
      slidesToScroll: 7,
      autoplay: false,
      dots: true,
      infinite: false,
      centerMode: false,
      variableWidth: true,
      variableHeight: true,
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
      centerPadding: "0px",
      responsive: [
        { breakpoint: 540,
            settings: { slidesToShow: 3, slidesToScroll: 3, }
        },
        { breakpoint: 940,
            settings: { slidesToShow: 5, slidesToScroll: 4, }
        },
        { breakpoint: 1120,
            settings: { slidesToShow: 6, slidesToScroll: 5, }
        },
        { breakpoint: 1300, 
          settings: 
            { slidesToShow: 7, slidesToScroll: 6, }
        }
      ]
    }

    
    if (this.props.movies.length < 1) {
      return ('loading')
    } else {
      const items = this.props.movies.map(movie => {
        return <Item key={movie.id} id={movie.id} title={movie.title} img={movie.poster} slug={movie.slug}/>
      })

      return (
        <div className="movie-carousel">
          <Slider {...settings}>
            {items}
          </Slider>
        </div>
      )

    }
  }
}

const Item = ({title, img, slug}) => (
  <div className="poster-wrapper mx-0">
    <Link to={slug} >
      <div className="poster-image">
        <img className="movie-poster" src={`${process.env.GATSBY_API_URL}${img}`} />
      </div>
      <div className="poster-title text-dark"> {title} </div>
    </Link>
  </div>
)
